import Select from 'react-select';
 
export function UniteMesure({ handleChangeInput, postData }) {
  const measurementUnits = [
    { value: 'cm', label: 'Centimtres (cm)' },
    { value: 'm', label: 'Mtres (m)' },
    { value: 'cm2', label: 'Centimtres carrs (cm)' },
    { value: 'm2', label: 'Mtres carrs (m)' },
    { value: 'mm', label: 'Millimtres (mm)' },
    { value: 'in', label: 'Pouces (in)' }
  ];

  return (
    <div className="unit-selector-container">
      <Select
        options={measurementUnits}
        onChange={(selectedOption) => handleChangeInput({
          target: {
            name: 'measurementUnit', // Nombre diferente para no confundir con el tamao
            value: selectedOption?.value || '',
            type: 'text'
          }
        })}
        name="measurementUnit"
        value={measurementUnits.find(opt => opt.value === postData?.measurementUnit) || null}
        placeholder="Slectionnez l'unit"
        className="unit-select"
        classNamePrefix="us"
        isSearchable={false}
        noOptionsMessage={() => "Aucune unit disponible"}
        styles={{
          control: (base) => ({
            ...base,
            minHeight: '42px',
            borderColor: '#ced4da',
            '&:hover': { borderColor: '#80bdff' }
          })
        }}
      />
    </div>
  );
}