import React from 'react';
import { Form  } from 'react-bootstrap';
import Select from 'react-select';
export function Envolverlaobra({ handleChangeInput, postData }) {
    const  Optionsenvolverobra = [
        { value: 'toile', label: 'Toile sur chssis (sans cadre)' },
        { value: 'toile_encadree', label: 'Toile encadre' },
        { value: 'papier', label: 'uvre sur papier (sous verre)' },
        { value: 'papier_plaque', label: 'Papier contrecoll sur plaque' },
        { value: 'boite', label: 'Bote de protection' },
        { value: 'tube', label: 'Roule dans un tube' },
        { value: 'caisse', label: 'Caisse en bois sur mesure' },
        { value: 'sculpture_boite', label: 'Sculpture en bote protectrice' },
        { value: 'montage_plexi', label: 'Montage sous plexiglas' },
        { value: 'autre', label: 'Autre conditionnement' }
    ];

    return (
        <div>
            <Form.Text className="text-muted">
                Indiquez comment votre uvre sera protge pour l'envoi
            </Form.Text>
        <Select
          options={Optionsenvolverobra}
          onChange={(selectedOption) => handleChangeInput({
            target: {
              name: 'envolverobra',
              value: selectedOption?.value || '',
              type: 'text',
              checked: undefined
            }
          })}
          name="envolverobra"
          value={postData ? Optionsenvolverobra.find(opt => opt.value === postData.envolverobra) : null}
          placeholder="Conditionnement de l'uvre"
          
        />
      </div>

 
    );
}